      return `https://groupincome.org/dummy-qr${this.dummyQueryString}`
    }
  },
  methods: {
    updateItem ({ index, ...data }) {
      Vue.set(this.form.paymentsToSend, index, {
        ...this.form.paymentsToSend[index],
        ...data
      })
    },
    onMemoUpdate ({ target }) {
      this.form.memo = target.value
    }
  }
